import { StatusBar } from '@ionic-native/status-bar'
import { IonPage } from '@ionic/react'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { RouteComponentProps } from 'react-router'

import { useClearStore } from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import {Capacitor} from "@capacitor/core";
import {OneSignal as OneSignalCapacitor} from "@ionic-native/onesignal";

const video = require('../../assets-ap/logout.mp4')
const poster = require('../../assets-ap/fondo-blanco.jpg')

export const Logout: React.FC<RouteComponentProps> = observer(({ history }) => {
  const clearStore = useClearStore()
  const rootStore = useStores()
  const { loginStore } = rootStore

  const setup = () => {
    if (Capacitor.getPlatform() !== 'web') {
      OneSignalCapacitor.getIds().then((ids) => {
        loginStore.environment.api.logout(ids.userId).then((result: any) => {
          console.log('Datos de onesignal enviados al backend', result)
        })
      })
    }
    StatusBar.overlaysWebView(false)
    //
    if (loginStore.modo_uso_app === 30) {
      rootStore.environment.locationService.stopUserTracking()
    }
    //
    setTimeout(() => {
      return clearStore()
    }, 4000)
  }

  React.useEffect(setup, [])

  return (
    <IonPage>
      <div
        className={'d-flex flex-column flex-grow-1 align-self-stretch justify-content-center align-items-center'}
        style={{ height: '100vh', background: '#FFF' }}
      >
        <video
          autoPlay
          muted={!loginStore.sonidos_activos}
          width="350"
          height="350"
          poster={poster.default}
          controls={false}
          playsInline
        >
          <source src={video.default} type="video/mp4" />
        </video>
      </div>
    </IonPage>
  )
})

export default Logout
